@import url(https://fonts.googleapis.com/css?family=Lobster|Lora:400,400i,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#main {
  display: flex;
  flex-direction: column;
  height: 100vh;
  text-align: center;
  background-color: var(--main-bg-color);
  font-family: 'Times New Roman', serif;
  font-size: 1rem;
  color: var(--main-txt-color);
}

#title {
  color: #fff;
  font-family: 'Lobster', cursive;
  font-weight: 400;
}

#quote-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  margin: 0 auto;
  width: 80%;
  height: 80vh;
  border-radius: 6vh;
  background-color: #fff;
}

#text {
  width: 90%;
  margin: 0 auto;
  font-size: 4vh;
  text-align: center;
  font-style: italic;
}

#author {
  text-align: right;
  margin-right: 10%;
  font-size: 2.5vh;
}

#new-quote, #tweet-quote {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 50%;
  height: 7.5%;
  line-height: 100%;
  border: none;
  border-radius: 6vh;
  background-color: var(--main-bg-color);
  color: #fff;
  margin: 1.5% auto;
  font-family: "Roboto Thin", sans-serif;
  font-size: 2.5vh;
}
a{
  display: block;
  text-decoration: none;
}

#new-quote:focus, #tweet-quote:focus {
  outline: none;
}

#new-quote:hover, #tweet-quote:hover {
  background-color: #fff;
  color: var(--main-txt-color);
  border: .1vh solid var(--main-txt-color);
}

@media screen and (orientation: landscape) {
  #quote-box {
    width: 50%;
    border-radius: 8vw;
  }

  #text {
    font-size: 2rem;
  }

  #author {
    font-size: 1.5rem;
  }

  #new-quote, #tweet-quote {
    height: 10%;
    font-size: 1.25rem;
  } 
}
